<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        width="220"
        :formatter="wwIdFormatter"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="交易客户"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="仓单货物品种"
        sortable
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="仓单总价值/总现价"
        :formatter="priceFormatter"
        sortable
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="对应总放款金额"
        sortable
        width="140"
      >
      </el-table-column>
      <el-table-column
        prop="tradeEndtime"
        label="结清日期"
        sortable
        width="105"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.tradeEndtime)}}
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import * as ajaxUrl from '../ajax.config.js';// 引入接口地址

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam:{},
      pageType: 4,
      tableData: [],

      currentPage: 1,
      pageSize: 10,
      totalPage: 100
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText; 
      this.axios.get(ajaxUrl.GETSETTLELIST, {
        params: {
          pageSize: this.pageSize,
          pageNum: this.currentPage,
          search: this.searchParam,
          orderField: orderField,
          orderType: orderType,
          status: true
        }
      }).then(res => {
        this.tableData = res.data.resData;
        this.totalPage = res.data.page.total;
        this.pageSize = res.data.page.pageSize;
        this.searchParam = {};
      }).catch(err => {
        console.log(err)
      })
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "-";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    //价格格式转换
    priceFormatter(row, column) {
      return row.originalPrice + " / " + row.amount * row.goodsPrice;
    },
    //时间格式转换
    dateFormat(data) {
      return this.common.fullTime(data);
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
</style>



